.login {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  overflow: hidden;
  color: rgb(168, 158, 158);
  &__login-box {
    display: flex;
    align-items: center;
    position: relative;
  }
  &__login-span {
    font-size: 2rem;
    font-weight: 600;
    margin-left: 7%;
  }
  &__boubles {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: radial-gradient(
      circle,
      rgba(255, 216, 41, 1) 0%,
      rgba(255, 255, 144, 1) 100%
    );
    &--one {
      position: absolute;
      right: 55px;
      box-shadow: -2px -3px 16px 0px $main-color;
    }
    &--two {
      width: 80px;
      height: 80px;
      position: absolute;
      right: -40px;
      box-shadow: -3px 0 10px 0px $main-color;
    }
    &--three {
        width: 80px;
      height: 80px;
      position: absolute;
      left: -40px;
      box-shadow: -3px 0 10px 0px $main-color;
    }
    &--four {
        position: absolute;
      left: 55px;
      box-shadow: -2px -3px 16px 0px $main-color;
    }
  }
  &__signUp-box {
    display: flex;
    align-items: center;
    position: relative;
    justify-content: flex-end;
  }
  &__signUp-span {
    font-size: 2rem;
    font-weight: 600;
    margin-right: 7%;
  }
  &__form-wrapper {
      width: 100%;
      height: 40%;
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: center;
  }
  &__inputs-box {
      width: 92%;
      height: 60%;
      border: 1px solid black;
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: center;
      border-radius: 8px;
  }
  &__username,
  &__password {
      width: 80%;
      padding: 5px 0 5px 30px;
      border: none;
      border-bottom: 1px solid $main-color;
      font-size: 1.2rem;
      outline: none;
  }
}
